<script>
  import { fly } from "svelte/transition";
  import ContextMenu from "./ContextMenu.svelte";
  import Draggable from "./Draggable.svelte";
  import { SvelteToast, toast } from '@zerodevx/svelte-toast'
  //#region circles class settings
  let i = 0;
  let undoStack = [[]];
  let circles = [],
    buttons = [];
  let selected;
  let mode = "create";
  let adjusting = false,
    adjusted = false,
    _export = false,
    status = false,
    assign = false;
  let _svgcode = "",
    _circlescode = "",
    _circlesstr = "",
    _boxesstr = "",
    steps = "",
    _imgcode = "";
  let imgUrl =
    "http://T01014110/EB/CPC/WKF_OptionalFields/ALL/OTS/DEMO/default.png";
  let data = {
    mode: mode,
    test_ID: "",
    dept_code: "",
    test_code: "",
    svg_string: _circlescode,
    img_string: _imgcode,
    svg_tag: _svgcode,
    steps: steps,
    circles: {},
    boxes: {},
  };
  //#endregion
  //#region Event function
  const onKeyPress = (e) => {
    if (e.charCode === 13) importIMG(1);
  };
  //點擊後設置物件
  function handleClick(event) {
    if (adjusting) {
      adjusting = false;
      // if circle was adjusted,
      // push to the stack
      if (adjusted) push();
      return;
    }
    if (mode == "edit") {
      mode = "update";
      return;
    }
    if (status != false) {
      status = false;
      return;
    }
    const circle = {
      cx: event.offsetX,
      cy: event.offsetY,
      r: 50,
      value: "",
      ToNext: false,
      ToPrevious: false,
      Muti: false,
      Check: false,
      step: 0,
    };
    circles = circles.concat(circle);
    selected = circle;
    push();
  }
  //調整物件大小
  function adjust(event) {
    selected.r = +event.target.value;
    circles = circles;
    adjusted = true;
  }
  //物件選定
  function select(circle, event) {
    if (!adjusting) {
      assign = false;
      event.stopPropagation();
      selected = circle;
    }
  }

  function push() {
    const newUndoStack = undoStack.slice(0, ++i);
    newUndoStack.push(clone(circles));
    undoStack = newUndoStack;
  }

  function travel(d) {
    circles = clone(undoStack[(i += d)]);
    adjusting = false;
  }

  function clone(circles) {
    return circles.map(
      ({ cx, cy, r, ToNext, ToPrevious, Muti, Check, value, step }) => ({
        cx,
        cy,
        r,
        ToNext,
        ToPrevious,
        Muti,
        Check,
        value,
        step,
      })
    );
  }

  function writeSvg() {
    _export = 1;
    status = false;
    setTimeout(() => {
      let img_content = document.getElementById("image");
      _imgcode = img_content.outerHTML;
      let svg_content = document.getElementById("wip");
      let circlesTag = document.getElementsByTagName("a");
      // let circlesTag = document.getElementsByTagName("circle");
      //SVG字串串接
      for (var n = 0; n < circlesTag.length; n++) {
        _circlescode += circlesTag[n].outerHTML;
      }
      _circlesstr = JSON.stringify(circles);
      //TextBox物件字串
      _boxesstr = JSON.stringify(dropped);
      localStorage.setItem("temp", svg_content.innerHTML);
      svg_content.innerHTML = "";
      _svgcode = svg_content.outerHTML;
      CreateFetch(_svgcode, _circlescode, _circlesstr, _boxesstr);
      svg_content.innerHTML = localStorage.getItem("temp");
      localStorage.removeItem("temp");
    }, 100);
  }

  function importIMG(val) {
    if (val == 1) mode = "create";
    else if (val == 0) mode = "create";
    else mode = false;
  }

  function editsvg() {
    mode = "edit";
    _export = false;
  }
  //#endregion
  //#region TextBox class settings
  let statustext = "waiting drop...";
  let obj_id = 0;
  const object = {
    id: 0,
    top: 100,
    left: 100,
    width: 100,
    height: 2,
    value: "",
    stabled: false,
    checked: false,
  };
  let objects = [object];

  let dropped = [];
  let box_selected;
  let dropped_in = "";
  box_selected = object;
  let moving = false;
  let left = 100,
    top = 100;
  //#endregion
  //#region TextBox Event functions
  function onMouseMove(e) {
    if (moving) {
      var id = e.target.id;
      if (id != "image") {
        var temp = JSON.parse(JSON.stringify(dropped[id - 1]));
        left = temp.left;
        top = temp.top;
        left += e.movementX;
        top += e.movementY;
        temp.left = left;
        temp.top = top;
        dropped[id - 1] = temp;
        console.log(dropped);
      }
    }
  }

  function onMouseDown() {
    if(e.target.id != "image")
      moving = true;
  }

  function onMouseUp() {
    moving = false;
  }

  function Remove() {
    console.log(dropped[0].id);
    if (obj_id != 0) {
      dropped = dropped.slice(0, dropped.length - 1);
      obj_id = obj_id - 1;
      statustext = "You have " + obj_id + " in area";
    }
  }

  function AddItem() {
    left = 100;
    top = 100;
    var temp = JSON.parse(JSON.stringify(object));
    temp.left = left;
    temp.top = top;
    obj_id = obj_id + 1;
    statustext = "You add " + obj_id + " into area";
    temp.id = obj_id;
    dropped = dropped.concat(temp);
    console.log(dropped);
  }

  function adjust_width(event) {
    box_selected.width = +event.target.value;
    // selected.height = +event.target.value;
    // objects = objects;
  }
  function adjust_height(event) {
    box_selected.height = +event.target.value;
    // selected.height = +event.target.value;
    // objects = objects;
  }
  //#endregion
  //#region Fetch data
  //輸出資料
  function CreateFetch(_svgcode, _circlescode, _circlesstr, _boxesstr) {
    data = {
      mode: mode,
      test_ID: data.test_ID,
      test_code: data.test_code,
      dept_code: data.dept_code,
      svg_string: _circlescode,
      img_string: imgUrl,
      svg_tag: _svgcode,
      steps: data.steps,
      circles: _circlesstr,
      boxes: _boxesstr,
    };
    if (data.img_string != "") {
      fetch("http://172.16.13.174:18472/OTS", {
        method: "POST",
        body: JSON.stringify(data),
        headers: {
          "Content-Type": "application/json",
          Authorization: "",
        },
        mode: "cors",
        cache: "default",
      })
        .then((response) => {
          //console.log(response);
        })
        .catch((error) => {
          console.log(`Error: ${error}`);
        });
    }
  }
  //請求資料
  function SearchFetch(info) {
    if (info) data = info;
    data.mode = "search";
    if (data.mode == "search") {
      fetch("http://172.16.13.174:18472/OTS-json", {
        method: "POST",
        body: JSON.stringify(data),
        headers: {
          "Content-Type": "application/json",
          Authorization: "",
        },
        mode: "cors",
        cache: "default",
      })
        .then((response) => response.json()) //console.log(response.json())
        .then((data) => {
          if (data.length != 1) {
            for (var k = 0; k < data.length; k++) {
              buttons[k] = data[k];
            }
            buttons.sort(function (a, b) {
              return a.steps - b.steps;
            });
            mode = "edit";
          } else {
            const newdata = data[0];
            data = {
              test_ID: newdata.test_ID,
              dept_code: newdata.dept_code,
              test_code: newdata.test_code,
              svg_string: newdata.svg_string,
              img_string: newdata.img_string,
              svg_tag: newdata.svg_tag,
              steps: newdata.steps,
              circles: newdata.circles,
              boxes: newdata.boxes,
            };
            Restruct(data);
          }
        })
        .catch((error) => {
          console.log(`Error: ${error}`);
        });
    }
  }
  //導入已設計完成場景
  function Restruct(data) {
    toast.push("Loading...",{
      theme:{
      '--toastBackyround': 'rgba(0, 201, 87, 0.95)'}
    })
    //各項參數歸零
    circles = [];
    dropped = [];
    i = 0;
    obj_id = 0;
    //圓形物件設定
    var temp = JSON.parse(data.circles);
    if (undoStack.length <= 0) {
      for (var n = undoStack.length - 1; n >= 1; n--) {
        undostack = undoStack.slice(0, n);
      }
    }
    for (var k = 0; k <= temp.length - 1; k++) {
      var obj = temp[k];
      const circle = {
        cx: obj["cx"],
        cy: obj["cy"],
        r: obj["r"],
        value: obj["value"],
        ToNext: obj["ToNext"],
        ToPrevious: obj["ToPrevious"],
        Muti: obj["Muti"],
        Check: obj["Check"],
        step: obj["step"],
      };
      var copy = JSON.parse(JSON.stringify(circle));
      circles = circles.concat(copy);
      selected = circle;
      push();
    }
    //TextBox物件設定
    if (data.boxes != null) {
      var temp = JSON.parse(data.boxes);
      for (var k = 0; k <= temp.length - 1; k++) {
        var obj = temp[k];
        const newbox = {
          id: obj["id"],
          top: obj["top"],
          left: obj["left"],
          width: obj["width"],
          height: obj["height"],
          value: obj["value"],
          stabled: true,
          checked: obj["checked"],
        };
        var copy = JSON.parse(JSON.stringify(newbox));
        dropped = dropped.concat(copy);
        obj_id = obj_id + 1;
        statustext = "You dropped " + obj_id + " into area";
      }
    }
    //底圖設定
    let image = document.querySelector("#image");
    image.setAttribute("href", data.img_string);
    imgUrl = data.img_string;
    let svg = document.querySelector("svg");
    //調整圖層排序
    for (var k = 0; k < svg.childNodes.length - 1; k++) {
      svg.appendChild(svg.firstChild);
    }
    //#endregion
  }
  
  function setEvent() {}
</script>

<div class="controls">
  <button on:click={() => importIMG()}>import</button>
  <button on:click={() => editsvg()}>edit</button>
  <button on:click={() => travel(-1)} disabled={i === 0}>undo</button>
  <button
    on:click={() => travel(+1)}
    disabled={i === undoStack.length - 1 || i < 0}>redo</button
  >
  <button on:click={() => writeSvg()}>publish</button>
  <!-- <button on:click={() => toast.push('Hello world!')}>EMIT TOAST</button> -->
</div>
<!-- svelte-ignore a11y-click-events-have-key-events -->

<span style="position:relative;top: 40px;">
  <!-- viewBox="400 0 1000 1000" preserveAspectRatio="xMidYMid meet" width = "1800" -->
  <svg id="wip" on:click={handleClick}>
    <image id="image" href={imgUrl} width="1800" />
    {#each circles as circle}
      {#if _export}
        <a xlink:href=" " class="svg">
          <circle
            cx={circle.cx}
            cy={circle.cy}
            r={circle.r}
            fill="white"
            fill-opacity="0.1"
            on:click={(event) => select(circle, event)}
            on:contextmenu|stopPropagation|preventDefault={() => {
              adjusting = !adjusting;
              if (adjusting) selected = circle;
            }}
          />
        </a>
      {:else}
        <!-- svelte-ignore a11y-click-events-have-key-events -->
        <circle
          cx={circle.cx}
          cy={circle.cy}
          r={circle.r}
          on:click={(event) => select(circle, event)}
          on:contextmenu|stopPropagation|preventDefault={() => {
            adjusting = !adjusting;
            if (adjusting) selected = circle;
          }}
          fill={circle === selected ? "#ccc" : "white"}
          fill-opacity="0.6"
        />
      {/if}
    {/each}
  </svg>
</span>
<!-- TextBox Object Start-->
{#each dropped as drop}
  {#if drop.stabled == false}
    <Draggable>
      <div
        on:mousedown={onMouseDown}
        class="objects"
        style="cursor: auto; value:{drop.value};width:{drop.width}px; height:{drop.height}px"
      >
        <input
          id={drop.id}
          type="text"
          style="width: {drop.width}px; height:{drop.height}px;"
          bind:value={drop.value}
        />
      </div>
    </Draggable>
  {:else}
    <section
      style="cursor: auto;top:{drop.top}px; left:{drop.left}px; position: absolute;"
    >
      <div
        class="objects"
        style="cursor: auto; value:{drop.value};width:{drop.width}px; height:{drop.height}px"
      >
        <input
          id={drop.id}
          type="text"
          style="width: {drop.width}px; height:{drop.height}px;"
          bind:value={drop.value}
        />
      </div>
    </section>
  {/if}
{/each}
<svelte:window on:mouseup={onMouseUp} on:mousemove={onMouseMove} />
<!-- TextBox Object End -->
<!-- Toast Component Start-->
<SvelteToast options={{ reversed: true, intro: { y: 192 } }} />
<!-- Toast Component End-->
<!-- Circles config Setting Sidebar Start-->
{#if adjusting || _export}
  <nav class="sidebar" transition:fly={{ x: 250, opacity: 1 }}>
    <br />
    <br /><br />
    <div class="adjuster">
      {#if _export}
        <p>輸出完成資訊如下，請重整後再繼續</p>
        <table>
          <tr><td>test_ID:</td><td>{data.test_ID}</td></tr>
          <tr><td>dept_code:</td><td>{data.dept_code}</td></tr>
          <tr><td>test_code:</td><td>{data.test_code}</td></tr>
          <tr><td>steps:</td><td>{data.steps}</td></tr>
        </table>
      {:else}
        <p>adjust diameter of circle at {selected.cx}, {selected.cy}</p>
        <input type="range" value={selected.r} on:input={adjust} />
        <p>Hint Text</p>
        <textarea
          bind:value={selected.value}
          style="width: 290px; height: 100px;"
        />
        <p>
          <label
            ><input
              type="checkbox"
              bind:checked={selected.ToPrevious}
              style="width: 20%;"
            />跳轉上一個場景</label
          >
          <label
            ><input
              type="checkbox"
              bind:checked={selected.ToNext}
              style="width: 20%;"
            />跳轉下一個場景</label
          >
          <label
            ><input
              type="checkbox"
              bind:checked={assign}
              style="width: 20%;"
            />指定跳轉場景</label
          >
          {#if assign}
            <label style="left:20%; position:relative;"
              >步驟:<input
                type="number"
                bind:value={selected.step}
                style="width: 20%;"
              />
            </label>
          {/if}
          <label
            ><input
              type="checkbox"
              bind:checked={selected.Muti}
              style="width: 20%;"
            />設為多重選項</label
          >
        </p>
      {/if}
    </div>
  </nav>
{/if}
{#if mode == false}
  <nav class="sidebar" transition:fly={{ x: 500, opacity: 1 }}>
    <br />
    <br /><br />
    <div class="import" style="text-align:center;">
      <p style="text-align: left;">import IMG</p>
      <input
        type="text"
        bind:value={imgUrl}
        width="300"
        on:keypress={onKeyPress}
      />
      <p style="text-align: left;">basic setting</p>
      <table>
        <tr>
          <td>test_ID</td>
          <td><input type="text" bind:value={data.test_ID} /></td>
        </tr>
        <tr>
          <td>dept_code</td>
          <td><input type="text" bind:value={data.dept_code} /></td>
        </tr>
        <tr>
          <td>test_code</td>
          <td><input type="text" bind:value={data.test_code} /></td>
        </tr>
        <tr>
          <td>steps</td>
          <td><input type="text" bind:value={data.steps} /></td>
        </tr>
      </table>
      <button on:click={() => importIMG(0)}>取消</button>
      <button on:click={() => importIMG(1)}>確認</button>
    </div>
  </nav>
{/if}
{#if mode == "edit"}
  <nav class="sidebar" transition:fly={{ x: 500, opacity: 1 }}>
    <br />
    <br /><br />
    <div class="edit" style="text-align:center;">
      <p>輸入條件</p>
      <table>
        <tr>
          <td>img_url</td>
          <td><input type="text" bind:value={imgUrl} /></td>
        </tr>
        <tr>
          <td>test_ID</td>
          <td><input type="text" bind:value={data.test_ID} /></td>
        </tr>
        <tr>
          <td>dept_code</td>
          <td><input type="text" bind:value={data.dept_code} /></td>
        </tr>
        <tr>
          <td>test_code</td>
          <td><input type="text" bind:value={data.test_code} /></td>
        </tr>
        <tr>
          <td>steps</td>
          <td><input type="text" bind:value={data.steps} /></td>
        </tr>
      </table>
      <button on:click={() => (mode = "create")}>取消</button>
      <button on:click={() => SearchFetch()}>確定</button>
    </div>
    {#if buttons.length > 1}
      <div class="scrollbox" style="overflow:auto;height:200px;width:80%;">
        <div class="scrollbox-inner">
          {#each buttons as button}
            <button on:click={() => SearchFetch(button)}
              >ID:{button.test_ID}_CODE:{button.test_code}_步驟:{button.steps}</button
            >
          {/each}
        </div>
      </div>
    {/if}
  </nav>
{/if}
<!-- Circles config Setting Sidebar End-->
<!-- TextBox config Setting Sidebar Start-->
{#if status == "TextBox"}
  <nav class="text-sidebar" transition:fly={{ x: 500, opacity: 1 }}>
    <h2 id="app_status">Drag status: {statustext}</h2>
    <h1>Drop Zone</h1>
    <p>
      {#each objects as obj}
        <div id={obj.id} class="objects" draggable="true">
          <input
            type="text"
            disabled="true"
            style="width:{box_selected.width}px;height:{box_selected.height}px;"
          />
        </div>
      {/each}
    </p>
    <div class="Btn">
      <p>width:{box_selected.width}px</p>
      <input
        type="range"
        value={box_selected.width}
        on:input={adjust_width}
        min="0"
        max="300"
      />
      <br />
      <p>height:{box_selected.height}px</p>
      <input
        type="range"
        value={box_selected.height}
        on:input={adjust_height}
        min="0"
        max="100"
      />
      <table>
        <tr>
          <td>
            <h3>是否於此驗證</h3>
          </td>
          <td>
            <input type="checkbox" bind:checked={box_selected.checked} />
          </td>
        </tr>
      </table>
      <button on:click={() => AddItem()}>新增一個文字方塊</button>
      <button on:click={() => Remove()}>移除最近一個文字方塊</button>
    </div>
  </nav>
{/if}
<!-- TextBox config Setting Sidebar End-->
<!-- right click list menu -->
<ContextMenu bind:status/>

<style type="text/css">
